html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

.App{font-family:'Roboto', sans-serif}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.Navbar{width:100vw;height:11vh;background-color:#2094f3;font-family:'Chilanka', cursive;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23);display:flex;justify-content:space-between;align-items:center}.Navbar__Name{display:flex;justify-content:center;align-items:center;width:40vw;font-size:clamp(1rem, 2.25vw, 2rem)}.Navbar__Name img{width:12%;border-radius:50%;margin-right:15px}.Navbar__Name h5{color:#fff;cursor:pointer}.Navbar__Name h5:hover{color:#99d5ff}.Navbar__Links{display:flex;justify-content:space-evenly;align-items:center;width:60vw;font-size:clamp(0.75rem, 2vw, 1.5rem)}.Navbar__Links li{color:#fff;cursor:pointer;width:10px}.Navbar__Links li:hover{color:#99d5ff}.Navbar a{text-decoration:none}

.App{font-family:'Roboto', sans-serif}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.Landing{display:flex;flex-direction:column}.Landing__Container{display:flex;justify-content:space-evenly;flex-wrap:wrap;height:44vh}.Landing__Mission,.Landing__Founder{min-width:500px;width:40vw;background-color:#f5b938;color:#fff;border-radius:7px;margin:50px 0;padding:15px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.Landing__Mission h5,.Landing__Founder h5{text-align:center;font-weight:bold;font-size:clamp(1rem, 2vw, 1.75rem)}.Landing__Mission ul,.Landing__Founder ul{list-style:outside;margin:20px 0 0 30px}.Landing__Mission ul li,.Landing__Founder ul li{padding:5px 0;font-size:clamp(1rem, 2vw, 1.25rem)}.Landing__Founder{background-color:#343665}.Slideshow{display:flex;width:100vw;height:45vh;background-color:#d2191f}.Slideshow__Background{width:100vw;height:45vh;background-color:#1e5885;-webkit-clip-path:polygon(0 0, 75% 0, 100% 100%, 25% 100%);clip-path:polygon(0 0, 75% 0, 100% 100%, 25% 100%);position:relative}.Slideshow__Images{position:absolute;height:100%;width:100%;display:flex;justify-content:center;opacity:0;transition:opacity 0.04s ease-in-out}.Slideshow__Images img{object-fit:contain;border-radius:7px}.Slideshow .Active{opacity:1;transition-duration:2s}

.App{font-family:'Roboto', sans-serif}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.Voting{display:flex;justify-content:space-evenly;flex-wrap:wrap;position:relative}.Voting__Info{display:flex;flex-direction:column;align-items:center;width:600px;height:600px;margin-top:50px;border-radius:7px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.Voting__Info h5{text-align:center;padding:20px 0;font-size:clamp(1rem, 2.25vw, 1.75rem)}.Voting__Info p{font-size:clamp(1rem, 2vw, 1.25rem)}.Voting__Info img{width:80%;height:100%;overflow:hidden;border-radius:7px;margin-top:20px;transition:transform 1s}.Voting__Info img:hover{transform:scale(1.25)}.Voting__Info ul{margin:10px 0}.Voting__Info li{list-style:outside;padding:5px 0}.Voting__Ballot{display:flex;flex-direction:column;align-items:center;width:600px;height:600px;margin-top:50px;border-radius:7px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23);overflow-y:scroll}.Voting__Ballot h5{text-align:center;padding:20px 0;font-size:clamp(1rem, 2.25vw, 1.75rem)}.Voting__Ballot img{width:500px;height:600px;transition:transform 1s;cursor:pointer}.Voting__View{position:absolute;width:100vw;height:200vh;background:rgba(128,128,128,0.8);display:flex;justify-content:center;cursor:pointer}.Voting__View .Voting__Image{width:50%;min-width:500px;height:100%}

.App{font-family:'Roboto', sans-serif}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.Events{display:flex;justify-content:space-evenly;flex-wrap:wrap-reverse;width:100vw}.Events__List{display:flex;flex-direction:column;align-items:center;width:600px;height:600px;margin-top:60px;overflow-y:scroll}.Events__Calendar{display:flex;flex-direction:column;align-items:center;width:600px;height:600px;margin-top:50px;border-radius:7px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.Events__Calendar iframe{width:100%;height:100%}.Events__Item{height:60px;width:80%;margin-bottom:30px;padding:15px;border-radius:7px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23);transition:transform 1s;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Events__Item h5{font-size:clamp(1rem, 2.25vw, 1.25rem);margin-bottom:5px}.Events__Item p{font-size:clamp(0.75rem, 2vw, 0.9rem)}.Events__Item:hover{transform:scale(1.1)}

.App{font-family:'Roboto', sans-serif}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.Social{display:flex;justify-content:space-evenly;flex-wrap:wrap}.Social__Image{display:flex;flex-direction:column;align-items:center;width:600px;height:600px;margin-top:50px;border-radius:7px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23);overflow:hidden}.Social__Image img{height:100%;width:130%}.Social__Contact{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;width:600px;height:600px;margin-top:50px;border-radius:7px;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.Social__Contact .Social__Facebook{text-align:center}.Social__Contact .Social__Facebook a{text-decoration:none}.Social__Contact .Social__Facebook h4{color:#2094f3;font-size:clamp(1.5rem, 2.25vw, 2rem);font-family:'Merriweather Sans', sans-serif;padding-bottom:20px}.Social__Contact .Social__Facebook h4:hover{color:#99d5ff}.Social__Contact .Social__Facebook h5{font-size:clamp(1rem, 1.5vw, 1.75rem)}.Social__Contact .Social__News h4{color:#1f242d;font-size:clamp(1.5rem, 2.25vw, 2rem);font-family:'Merriweather Sans', sans-serif;padding-bottom:20px;text-decoration:underline}.Social__Contact .Social__News .Social__Gabber{text-decoration:none}.Social__Contact .Social__News p{color:#1f242d;text-align:center;font-size:clamp(1.25rem, 1.75vw, 2rem)}.Social__Contact .Social__News ul{list-style:outside;margin:10px 0 0 110px}.Social__Contact .Social__News a{color:#2094f3}.Social__Contact .Social__News a:hover{color:#99d5ff}.Social__Contact .Social__News li{padding:5px 0}

